import React, { useEffect, useState, useRef } from "react";
import { completeLevel } from "../progressManager";
import { useNavigate } from "react-router-dom";

const createSvg = (tagName) => {
    const svgNS = "http://www.w3.org/2000/svg";
    return document.createElementNS(svgNS, tagName);
};

const Grid = ({ numberPerSide, size, pixelsPerSide, colors, id }) => {
    const containerRef = useRef(null);
    const navigate = useNavigate();

    useEffect(() => {
        const grid = (numberPerSide, size, pixelsPerSide, colors) => {
            const svg = createSvg("svg");
            svg.setAttribute("width", pixelsPerSide);
            svg.setAttribute("height", pixelsPerSide);
            svg.setAttribute(
                "viewBox",
                [0, 0, numberPerSide * size, numberPerSide * size].join(" ")
            );

            const cells = [];

            for (let i = 0; i < numberPerSide; i++) {
                cells[i] = [];
                for (let j = 0; j < numberPerSide; j++) {
                    const color1 = colors[(i + j) % colors.length];
                    const g = createSvg("g");
                    g.setAttribute(
                        "transform",
                        `translate(${i * size}, ${j * size})`
                    );
                    const number = numberPerSide * i + j;

                    const box = createSvg("rect");
                    box.setAttribute("width", size);
                    box.setAttribute("height", size);
                    box.setAttribute("fill", color1);
                    box.setAttribute("data-row", i);
                    box.setAttribute("data-col", j);
                    box.setAttribute("id", `b${number}`);
                    g.appendChild(box);

                    cells[i][j] = box; 

                    svg.appendChild(g);
                }
            }
            const middleRow = Math.floor(numberPerSide / 2);
            const middleCol = Math.floor(numberPerSide / 2);
            const middleCell = cells[middleRow][middleCol];
            middleCell.setAttribute("fill", colors[1]);

            svg.addEventListener("click", function (e) {
                const target = e.target;
                if (target.tagName === "rect") {
                    const row = parseInt(target.getAttribute("data-row"));
                    const col = parseInt(target.getAttribute("data-col"));

                    const neighbors = [
                        [row - 1, col],
                        [row + 1, col], 
                        [row, col - 1],
                        [row, col + 1],
                    ];

                    neighbors.forEach(([nRow, nCol]) => {
                        if (
                            nRow >= 0 &&
                            nRow < numberPerSide &&
                            nCol >= 0 &&
                            nCol < numberPerSide
                        ) {
                            const neighbor = cells[nRow][nCol];
                            const currentColor = neighbor.getAttribute("fill");
                            const newColor =
                                currentColor === colors[0] ? colors[1] : colors[0];
                            neighbor.setAttribute("fill", newColor);
                        }
                    });
                }
                let blacks = 0;
                for (let i = 0; i < numberPerSide; i++) {
                    for (let j = 0; j < numberPerSide; j++) {
                        const cell = cells[i][j];
                        const color = cell.getAttribute("fill");
                        if (color === "black") {
                            blacks += 1;
                        }
                    }
                };
                if (blacks === numberPerSide * numberPerSide) {
                    completeLevel(4);
                    setTimeout(navigate('/level-5'), 2000);
                }
            });

            return svg;
        };

        const gridElement = grid(numberPerSide, size, pixelsPerSide, colors);
        const container = containerRef.current;
        if (container) {
            container.innerHTML = ""; 
            container.appendChild(gridElement);
        }
    }, [numberPerSide, size, pixelsPerSide, colors, navigate]);

    return <div id={id} ref={containerRef}></div>;
};

const Level4 = () => {
    const [next, setNext] = useState(false);

    useEffect(() => {
        const timer = setTimeout(() => {
            setNext(true);
        }, 5000);

        return () => clearTimeout(timer);
    }, []);

    return (
        <div>
            {(next === false) ? (
                <div>
                    The <span style={{color: "red"}}>demon</span> cast a spell! <br/>
                    You have been <span style={{color: "green"}}>poisoned</span>!
                </div>
            ) : (
                <Grid
                    id="grid1"
                    numberPerSide={15}
                    size={50}
                    pixelsPerSide={600}
                    colors={["green", "black"]}
                />
            )}
        </div>
    );
};

export default Level4;
