import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import "../assets/styles/Level1.css";
import start from "../assets/images/start.gif";
import { completeLevel } from "../progressManager";
import hash from "hash.js";

const Level1 = () => {
    const [currentPhase, setCurrentPhase] = useState("");
    const [user, setUser] = useState('');
    const [response, setResponse] = useState('Dr. Goober, I presume?');
    const navigate = useNavigate();
    const hihiHaha = "849a2bd5d17ae2399f6c6ef073fcab5b8eca1d7c9dfb46eb75583aa178f50abc";

    const nextPage = () => {
        const uh = hash.sha256().update(user.toLowerCase()).digest("hex");
        if (uh === hihiHaha) {
            completeLevel(1);
            navigate("/level-2", { state: { user: user }});
        }
        else {

            const randResponses = [
                `What a stupid name, ${user}. Wrong!`, 
                `Surely, your name isn't ${user}. Try again!`,
                `Are you kidding, ${user}?`,
                `Try again, old man ${user}!`,
            ];
            setResponse(randResponses[Math.floor(Math.random()*randResponses.length)]);
        };
    };

    useEffect(() => {
        let time = 0;
        const introText = [
            "hello traveller", 
            "you need to help me!", 
            "an evil, fat demon has stolen all my soulstones!",
            "the demon is so ugly, that I can't beat him...",
            "only extremely ugly people can go near him",
            "oh, and you also need to be born in 1987",
            "on the 27th of December!",
            "that's why I am really happy to see you...",
            "only you can help me, ugly boy",
            "tell me, what's your name?", "INPUT"]

        const updatePhase = (sentence) => {
            setCurrentPhase(sentence);
        };

        introText.forEach((sentence) => {
            setTimeout(updatePhase, time, sentence);
            time += 3000; 
        });

        return () => {
            clearTimeout(time);
        };
    }, [navigate]);

return (
  <div className="landing-page">
    {currentPhase !== "INPUT" && (
      <div>
        <h1 className="welcome-title">{currentPhase}</h1>
      </div>
    )}
    {currentPhase === "INPUT" && (
      <div>
        <img src={start} alt="fire" className="fire-image" />
        <div className="content">
          <form
            onSubmit={(e) => {
              e.preventDefault();
              nextPage();
            }}
          >
            <input
              name="myInput"
              onChange={(e) => setUser(e.target.value)}
            />
          </form>
        </div>
        <div className="response">{response}</div>
      </div>
    )}
  </div>
);

};

export default Level1;

