import { React } from "react";
import { useNavigate } from "react-router-dom";
import "../assets/styles/Start.css";

const Start = () => {
    const navigate = useNavigate();
    
    const handleClick = () => {
        navigate('/level-1');
    };

    return (
      <div className="start" onClick={handleClick}>
      </div>
    );
}

export default Start;

