import React, { useEffect, useState } from "react";
import "../assets/styles/Level3.css";
import { useNavigate } from "react-router-dom";
import baby from '../assets/images/baby.gif';
import wrong from '../assets/images/wrong6.gif';
import egg from '../assets/images/egg.gif';
import { completeLevel } from "../progressManager";

const Level3 = () => {
    const [seconds, setSeconds] = useState(0); 
    const [start, setStart] = useState(false); 
    const [status, setStatus] = useState("Prove you are old enough to battle the demon!");
    const [win, setWin] = useState(false);
    const navigate = useNavigate();
    const [image, setImage] = useState(wrong);
    const slogans = [
      "Bro, that was skibidi level failure. Run it back!",
      "That attempt was straight-up bussin... but like, in a bad way. Do better!",
      "That was a whole vibe but not the good kind. Try again, fam!",
      "You got ratio'd on that one. Better luck next time, schlomo!",
      "Git gud, fam.",
      "That was mid at best. Go back to the grind, Justin Bieber!",
      "Bro, you're playing like a noob. Bussin' sussy baka!"
    ];

    const handleClick = () => {
        const randomSlogan = slogans[Math.floor(Math.random() * slogans.length)];
        if (start) {
            if (seconds === 37) {
                setStatus("Wow! You really are ancient!");
                setWin(true);
                setImage(egg);
                completeLevel(3);
                setTimeout(() => { navigate('/level-4') } , 8000);
            } else if (seconds < 37) {
                setStatus("Too fast! " + randomSlogan);
            } else {
                setStatus("Too slow! " + randomSlogan);
            }
        } else {
            setSeconds(0);
            setStatus("Tick tock tick tock, shoopelee hoop, tickity, tockity... schmick, smak, smok... tick, tack, tock..."); 
        }

        setStart((prevStart) => !prevStart);
    };

    useEffect(() => {
        let interval;
        if (start) {
            interval = setInterval(() => {
                setSeconds((prevSeconds) => prevSeconds + 1);
            }, 1000);
        }

        return () => clearInterval(interval);
    }, [start]);

    return (
        <div className="main">
          <div className="timer-container">
            <div className="timer-text">
              <p>{status}</p>
              {!start && seconds > 0 && (
                win ? (
                  <p>Congratulations, birthday boy!</p>
                ) : (
                  <p>Surely, you're not <span style={{ color: "red" }}>{seconds}</span> years old!</p>
                )
              )}
            </div>
            <div className="timer-image">
              {start ? (
                <img src={baby} alt="baby" className="gifs" />
              ) : (
                <img src={image} alt="wrong" className="gifs"/>
              )}
            </div>
          </div>
          <button className="timer-button" onClick={handleClick}>
            {start ? "Stop" : "Start"}
          </button>
        </div>
        );
};

export default Level3;

