import React, { useEffect, useState} from "react";
import { completeLevel } from "../progressManager";
import { useNavigate } from "react-router-dom";
import hard from "../assets/images/hard_big.png";
import hash from "hash.js";

const Level6 = () => {
    const [next, setNext] = useState(false);
    const [answer, setAnswer] = useState('');
    const navigate = useNavigate();
    const niettekraken = "a59597c3cb0c1b5c9ae2e720d40ad44eab8a6656652de3cafab6a009fdb9a4a5";

    const nextPage = () => {
        const lollercoaster = hash.sha256().update(answer).digest("hex");
        if (lollercoaster === niettekraken) {
            completeLevel(6);
            navigate("/level-7");
        }
    };
    useEffect(() => {
        const timer = setTimeout(() => {
            setNext(true);
        }, 5000);

        return () => clearTimeout(timer);
    }, []);

    return (
        <div>
            {(next === false) ? (
                <div style={{width: "400px"}}>
                    Oh no! You can only see two colors! You have been <span style={{color: "indigo"}}> blinded</span>!
                </div>
            ) : (
                <div style={{marginTop: "40px"}}>
                    <img src={hard} alt="nevergonnasolvethis" style={{objectFit: "contain", width: "auto", height:"auto"}}/> 
                    <form
                    onSubmit={(e) => {
                        e.preventDefault();
                        nextPage();
                    }}
                    >
                    <input style={{marginTop: "40px"}}
                        name="myInput"
                        onChange={(e) => setAnswer(e.target.value)}
                    />
                    </form>
                </div>
            )}
        </div>
    );
};

export default Level6;
