import { React, useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import "../assets/styles/Level7.css";
import { completeLevel } from "../progressManager";
import boss from '../assets/images/demonboss.png';
import state1 from '../assets/images/boss1.png';
import state2 from '../assets/images/boss2.png';
import state3 from '../assets/images/boss3.png';
import state4 from '../assets/images/boss4.png';
import state5 from '../assets/images/boss5.png';
import state6 from '../assets/images/boss6.png';
import state7 from '../assets/images/win.png';
import hash from "hash.js";

const Level7 = () => {
    const [backgroundImage, setBackgroundImage] = useState(state1);
    const navigate = useNavigate();
    const winWords = [
                "824a7d1134e55b2abb46e1ec77246825067d06c9c964c8a6d1caae7b306afd36", 
                "a85f4540a712162c6a4f04e3fb25a9863196ff63eaadf0d2a3e3b397e5f742d1", 
                "2344d78196ae6b8877c1a76a9a02a6b55608983efc1a08ed3448fc6d81d683b2", 
                "a3df42822fab233312b34920d6aef83ec8f0fbd287bdc12833de9dd8d4bf823e", 
                "270826d41d0a807d41e14d413e764fd30dab7a7bcd1e478ea94b9e812671042b", 
                "fd4bffc6c624dbbd7e0f5e762cf10893f3b38b631c310fe272fb19fd36014b9d"];
    const wLengths = [4, 3, 5, 5, 2, 3];
    const [winIdx, setWinIdx] = useState(0);
    const [typedSequence, setTypedSequence] = useState("");
    const bgMapping = {1: state1, 2: state2, 3: state3, 4: state4, 5: state5, 6: state6, 7: state7};
    const [next, setNext] = useState(false);

    useEffect(() => {
        const timer = setTimeout(() => {
            setNext(true);
        }, 9000);

        return () => clearTimeout(timer);
    }, []);

    useEffect(() => {
        if (bgMapping[winIdx]) {
            setBackgroundImage(bgMapping[winIdx+1]);
        }
    }, [winIdx, bgMapping]);

    useEffect(() => {
        const handleKeyPress = (event) => {
            const key = event.key.toUpperCase();

            if (/^[A-Z]$/.test(key)) {
                setTypedSequence((prev) => {
                    const newSequence = (prev + key).slice(-wLengths[winIdx]);
                    const hashpipe = hash.sha256().update(newSequence).digest("hex");
                    setWinIdx((prevWinIdx) => {
                        if (hashpipe === winWords[prevWinIdx]) {
                            if (prevWinIdx + 1 === 6) {
                                setTimeout(() => {completeLevel(7); navigate('/level-8')}, 5000); 
                            }
                            return prevWinIdx + 1;
                        }
                        return prevWinIdx; 
                    });

                    return newSequence; 
                });
            }
        };

        window.addEventListener("keypress", handleKeyPress);
        return () => window.removeEventListener("keypress", handleKeyPress);
    }, [winWords]); 

    return (
        <div>
        {(next === false) ? (
                <div>
                    A foul stinkin <span style={{color: "red"}}>demon</span> finally shows his face...<br/>
                    He is even uglier than you imagined! <br/> <br/>
                    <span style={{color: "yellow"}}>fighhttt me, hihihihi!</span> - the demon screams<br/>
                </div>
            ) : (
      <div className="main-page">
        {(winIdx === 6) ? (
                <img src={state7} alt="win" />
              ) : (
                <div className="boss" style={{backgroundImage: `url(${backgroundImage})`}}>
                    <img src={boss} alt="boss" className="boss-img"/>
                </div>
              )}
      </div>
    )}
      </div>
    )
}

export default Level7;
