import { React, useState } from "react";
import { useNavigate } from "react-router-dom";
import { useLocation } from "react-router-dom";
import "../assets/styles/Level2.css"; 
import lvl1 from '../assets/images/lvl1.png';
import lvl2 from '../assets/images/lvl2.png';
import lvl3 from '../assets/images/lvl3.png';
import lvl4 from '../assets/images/lvl4.gif';
import party from '../assets/images/gamer.png';
import { completeLevel } from "../progressManager";

const Level2 = () => {
    const location = useLocation();
    const user = location.state?.user || 'Pietertje';
    const [clicks, setClicks] = useState("?");
    const [image, setImage] = useState(false);
    const [png, selectPng] = useState(party);
    const [color, setColor] = useState("red");
    const [message, setMessage] = useState("37 years old!");
    const navigate = useNavigate();

    const handleClick = () => {
        setClicks((prevClicks) => {
            if (prevClicks === "?") {
                return 1;
            }
            return prevClicks + 1;
        });
        if (clicks === 36) {
            setTimeout(setImage(true), 2000);
        };
        const randomColor = `#${Math.floor(Math.random() * 16777215).toString(16)}`;
        setColor(randomColor);
    };

    const nextImage = () => {
        if (png === party) {
            selectPng(lvl1);
            setMessage("Wow, you're so old!");
        }
        else if (png === lvl1) {
            selectPng(lvl2);
            setMessage("Very sporty, but old...");
        }
        else if (png === lvl2) {
            selectPng(lvl3);
            setMessage("And you have four kids, geez...");
        }
        else if (png === lvl3) {
            selectPng(lvl4);
            setMessage("Happy birthday!");
        }
        else {
            completeLevel(2);
            setTimeout(navigate('/level-3'), 2000);
        }
    }

    return (
  <div className="main-page">
    {image ? (
      <div>
        <img onClick={nextImage} src={png} width="100%" alt="lvl1" className="visible top-image"/>
        <div className="message">
            {message} 
        </div>
      </div>
    ) : (
      <div>
        <div className="text">
          <p>
            Nice to meet you, {user}
            <span onClick={handleClick}>.</span>
            <br />
            How old are you<span style={{ color: "white" }}>...</span>
          </p>
        </div>
        <div className="click-counter looping-text" style = {{ color }}>
          {clicks}
        </div>
      </div>
    )}
  </div>
);
}

export default Level2;

