import React from "react";
import { HashRouter as Router, Routes, Route, Navigate } from "react-router-dom";
import { levels } from "./LevelConfig";
import { isLevelUnlocked } from "./progressManager";
import Start from "./pages/Start";
import icon1 from './assets/images/icon1.png';
import icon2 from './assets/images/icon9.png';
import icon3 from './assets/images/icon7.png';
import icon4 from './assets/images/icon4.png';
import icon5 from './assets/images/icon8.png';
import icon6 from './assets/images/icon6.png';
import icon7 from './assets/images/icon2.png';

function Header({ id }) {

    const iconMapping = {1: icon1, 2: icon2, 3: icon3, 4: icon4, 5: icon5, 6: icon6, 7: icon7};

    const unlockedIcons = Object.keys(iconMapping).filter(level =>
        isLevelUnlocked(parseInt(level)) && parseInt(level) < id
    );

    return (
        <header
        style={{
            position: "fixed",
                right: 0,
                top: 0,
                bottom: 0,
                color: "gray",
                padding: "10px 40px",
                textAlign: "right",
                marginTop: "10px",
                zIndex: 1000,
                fontSize: "40px"
        }}
        >
        <div>
        {unlockedIcons.map(level => (
          <img
            key={level}
            src={iconMapping[level]}
            alt={`Level ${level}`}
            style={{ marginBottom: "10px", width: "40px", height: "40px" }}
          />
        ))}
      </div>
        </header>
    );
}

function ProtectedRoute({ element: Component, levelId }) {
    return isLevelUnlocked(levelId) ? (
        <Component />
    ) : (
        <Navigate to={`/level-${levelId - 1}`} replace />
    );
}

function LevelRoute({ id, name, Component }) {
  return (
    <>
      <Header id={`${id}`} />
      <ProtectedRoute element={Component} levelId={id} />
    </>
  );
}

function App() {
  return (
    <Router>
      <Routes>
        <Route path="/" element={<Start />} />
        {levels.map(({ id, name, component: Component }) => (
          <Route
            key={id}
            path={`/level-${id}`}
            element={<LevelRoute id={id} name={name} Component={Component} />}
          />
        ))}
        <Route path="*" element={<div>404 - Level Not Found</div>} />
      </Routes>
    </Router>
  );
}

export default App;
